import uniPopup from '@/components/uni-popup/uni-popup.vue'
export default {
	components: {
		uniPopup
	},
	data() {
		return {
			type:'middle-list',
			kjshuoming:'<div style="font-size:16px;text-align:center;font-weight:600;line-height:30px;padding-top:10px">为了更好的服务您，请点击授权</div><div  style="font-size:12px;text-align:center;line-height:20px">授权仅包含昵称和头像信息</div>'
		};
	},
	methods:{
		getuserinfo(e){
			var that = this;
			if(e.detail.errMsg=="getUserInfo:ok"){
				uni.showLoading({
					title: '正在授权',
					mask: true
				});
				uni.login({
					provider: 'weixin',
					success: function (loginRes) {
						if(loginRes.errMsg=="login:ok"){
							var code = loginRes.code;
							uni.getUserInfo({
								provider: 'weixin',
								lang:'zh_CN',
								success: function (infoRes) {
									infoRes['token'] = uni.getStorageSync('cbe_token');
									uni.request({
										url:uni.getStorageSync('hostname') + '?action=index/member/wechat_mp&code='+code,
										data: infoRes,
										method:'POST',
										dataType:'json',
										header: {'content-type' : 'application/x-www-form-urlencoded'},
										success: (resa) => {
											uni.hideLoading();
											var one = resa.data;
											if(one.code==0){
												uni.showToast({
													title:one.msg,icon:'success',mask:true,duration: 3000
												});
												var timer = setTimeout(()=>{
													uni.navigateBack({
														delta:1
													})
												},3000);
											}else{
												uni.showToast({ title:one.msg, icon: "none" });
												this.logining = false;
											}
										}
									})
								}
							});
						}
					}
				});
			}
		},
	}
}
